import React from "react"
import s from "./SocialMedia.module.css"

const SocialMedia = ({ contacts }) => {
return (
   <>
    { contacts.facebook ? <div className={s.contacts}>My Facebook :<a className={s.contactsLink} href={contacts.facebook}>{contacts.facebook}</a></div> : null }
    { contacts.website ? <div className={s.contacts}>My website : <a className={s.contactsLink} href={contacts.website}>{contacts.website}</a></div> : null }
    { contacts.vk ? <div className={s.contacts}>My VK : <a className={s.contactsLink} href={contacts.vk}>{contacts.vk}</a></div> : null }
    { contacts.twitter ? <div className={s.contacts}>My Twitter :<a className={s.contactsLink} href={contacts.twitter}>{contacts.twitter}</a></div> : null }
    { contacts.instagram ? <div className={s.contacts}>My Instagram :<a className={s.contactsLink} href={contacts.instagram}>{contacts.instagram}</a></div> : null }
    { contacts.youtube ? <div className={s.contacts}>My YouTube chanel :<a className={s.contactsLink} href={contacts.youtube}>{contacts.youtube}</a></div> : null }
    { contacts.github ? <div className={s.contacts}>My github :<a className={s.contactsLink} href={contacts.github}>{contacts.github}</a></div> : null }
    { contacts.mainLink ? <div className={s.contacts}>Main Link :<a className={s.contactsLink} href={contacts.mainLink}>{contacts.mainLink}</a></div> : null }
    </>
    )
}

export default SocialMedia