import React from "react"
import { useState } from "react";
import s from "./editProfile.module.css"

const EditProfile = ({ userInfo, uploadNewInfo, profileEditModeDisable }) => {

    let styleShield = { resize: "none", width: "60%", fontSize: "20px" }
    // let styleShieldContacts = { resize: "none", width: "70%", fontSize: "20px", height: "25px", position: "relative", top: "5px" }

    let aboutMeValue = React.createRef();
    let lookingForAJobDescriptionValue = React.createRef();
    let fullNameValue = React.createRef();

    let [aboutMe, setAboutMe] = useState(userInfo.aboutMe);
    let [contacts, setContacts] = useState(userInfo.contacts);
    let [fullName, setFullName] = useState(userInfo.fullName);
    let [lookingForAJob, setLookingForAJob] = useState(userInfo.lookingForAJob);
    let [lookingForAJobDescription, setLookingForAJobDescription] = useState(userInfo.lookingForAJobDescription);

    let changeCheckboxLookingForAJOb = () => {
        if (lookingForAJob === false) {
            setLookingForAJob(true)
        } else {
            setLookingForAJob(false)
        }
    }

    let saveEditedInfo = () => {
        uploadNewInfo(aboutMe, fullName, lookingForAJob, lookingForAJobDescription, contacts)
    }

    return (
        <>
            <div className={s.typeEditDiv}>
                <div className={s.placeName}>Full Name:</div>
                <textarea ref={fullNameValue} value={fullName} style={styleShield} onChange={() => { setFullName(fullNameValue.current.value) }}></textarea>
            </div>
            <hr className={s.hr} />
            <div className={s.typeEditDiv}>
                <div className={s.placeName}>About me:</div>
                <textarea ref={aboutMeValue} value={aboutMe} style={styleShield} onChange={() => { setAboutMe(aboutMeValue.current.value) }}></textarea>
            </div>
            <div className={s.typeEditDiv}>
                <div className={s.placeName}>
                    <label className={s.container}>Looking for a job
                        <input type="checkbox" checked={lookingForAJob} onChange={changeCheckboxLookingForAJOb} />
                        <span className={s.checkmark}></span>
                    </label>
                </div>
            </div>
            {lookingForAJob ? <div className={s.typeEditDiv}>
                <div className={s.placeName}>Looking for a jod description:</div>
                <textarea ref={lookingForAJobDescriptionValue} value={lookingForAJobDescription} style={styleShield} onChange={() => { setLookingForAJobDescription(lookingForAJobDescriptionValue.current.value) }}></textarea>
            </div> : null}
            <hr className={s.hr} />
            <div className={s.typeEditDiv}>
                <div className={s.placeName}>Your Contacts:</div>
            </div>
            <button onClick={ () => {
                saveEditedInfo();
                profileEditModeDisable()}}>SAVE CHANGES</button>
        </>
    )
}

export default EditProfile;





